* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: Poppins, sans-serif;
}

header {
  width: 100%;
  background: #f0f3f4;
  padding: 12px 40px;
}

ul {
  list-style: none;
}

a {
  color: #17201f;
  text-decoration: none;
}

button {
  font-family: Poppins, sans-serif;
}

.header__nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 140px;
  height: 27px;
}

.header__menu {
  display: flex;
}

.menu__item {
  margin: 0 32px;
  font-size: 1.125em;
}

.header__div {
  align-content: center;
  align-items: center;
  display: flex;
}

.header__cart {
  height: 24px;
  width: 24px;
  background-color: #0000;
  border: none;
  align-items: center;
  margin: 0 16px 0 0;
  padding: 0;
}

.header__search {
  height: 24px;
  width: 24px;
  background-color: #0000;
  border: none;
  align-items: center;
  margin: 0 24px 0 0;
  padding: 0;
}

.header__btn {
  width: 140px;
  height: 40px;
  color: #f0f3f4;
  background: #93b1b1;
  border: 1px solid #17201f;
  border-radius: 5px;
  font-size: 1.125em;
}

.filters {
  height: 60px;
  background: #93b1b1;
  align-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.filters__label {
  color: #f0f3f4;
  margin-right: 16px;
}

.filters__filter {
  width: 100px;
  height: 20px;
  box-sizing: border-box;
  background: none;
  border: 1px solid #f0f3f4;
  border-radius: 5px;
  margin-top: auto;
  margin-bottom: 16px;
  margin-right: 16px;
  padding-left: 25px;
  font-family: Poppins;
  font-size: .625em;
  font-style: normal;
  font-weight: normal;
}

.catalog {
  width: 100%;
  background: #d7dce0;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 30px 135px;
  display: flex;
}

.products {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: block;
}

.products__box {
  width: 270px;
  height: 100%;
  margin: 30px 15px 0;
}

.products__img {
  height: 270px;
  width: 270px;
  object-fit: contain;
  background-color: #fff;
}

.products__details {
  width: 100%;
  height: 166px;
  background: #f0f3f4;
  padding: 24px 24px 13px;
}

.products__title, .products__price {
  color: #17201f;
  margin-bottom: 18px;
  font-size: 1.25em;
  font-style: normal;
  font-weight: normal;
}

.products__more {
  color: #17201f;
  font-size: .875em;
  font-style: normal;
  font-weight: normal;
}

.products__btn {
  width: 150px;
  height: 35px;
  box-sizing: border-box;
  color: #f0f3f4;
  background: #93b1b1;
  border: 1px solid #17201f;
  border-radius: 5px;
  font-family: Poppins;
  font-size: 1.125em;
  font-style: normal;
  font-weight: normal;
}

.footer {
  width: 100%;
  height: 410px;
  background: #93b1b1;
  flex-direction: column;
  align-content: center;
  align-items: center;
  padding-top: 111px;
  padding-bottom: 35px;
  display: flex;
}

.footer__list {
  align-items: center;
  margin-bottom: 114px;
  display: flex;
}

.footer__item {
  color: #f0f3f4;
  margin-left: 32px;
  margin-right: 32px;
  font-size: .875em;
  font-style: normal;
  font-weight: normal;
}

.footer__img {
  width: 140px;
  height: 27px;
}

.footer__socials {
  margin-bottom: 64px;
  display: flex;
}

.socials__item {
  margin-left: 12px;
  margin-right: 12px;
}

.socials__img {
  width: 40px;
  height: 40px;
}

.footer__copyright {
  color: #f0f3f4;
  text-align: center;
  font-size: .875em;
  font-style: normal;
  font-weight: normal;
}

